* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(159, 171, 224);
}

.App {
  font-family: Arial, Helvetica, sans-serif;
}

.displayTasks {
  width: 30%;
  margin: 0 auto;
  padding: 10px;
}

.cardBody {
  padding: 10px 25px;
}

.emptyDisplay {
  padding: 50px;
  margin: 5px auto;
  width: 100%;
}

@media only screen and (min-width: 319px) and (max-width: 767px) {
  .headerDiv {
    text-align: center;
  }

  .displayTasks {
    width: 80%;
    margin: 0 auto;
  }

  .mainCard {
    width: 100%;
    margin: 20px auto;
  }

  .formWrapper {
    margin: 10px 10px;
  }

  .form {
    width: 100%;
    margin: 0 auto;
  }

  .emptyDisplay {
    width: 100%;
    margin: 5px auto;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .appName {
    margin-left: 10px;
  }

  .displayTasks {
    width: 90%;
    margin: 0 auto;
  }

  .form {
    width: 70%;
    margin: 0 auto;
  }

  .mainCard {
    width: 50%;
    margin: 20px auto;
  }

  .emptyDisplay {
    width: 50%;
    margin: 5px auto;
  }
}

@media only screen and (min-width: 1024px) and (max-width: 1365px) {
  .appName {
    margin-left: 10px;
  }

  .displayTasks {
    width: 100%;
    margin: 0 auto;
    padding: 10px;
  }

  .form {
    width: 65%;
    margin: 0 auto;
  }

  .mainCard {
    width: 40%;
    margin: 20px auto;
  }

  .emptyDisplay {
    width: 40%;
    margin: 5px auto;
  }
}
